import { Description, Title, PinkContent, Wrapper, Image } from "./styled";
import vectorMap from "../../images/vector-maps.svg";
import LinkButton from "../LinkButton";

export const About = () => (
    <>
        <Wrapper>
            <Wrapper $content>
                <Title>Dziaamy w wojewdztwie lubelskim</Title>
                <Description>
                    Wykonujemy nowoczesne <PinkContent>instalacje elektryczne</PinkContent>, <PinkContent>wiatowodowe</PinkContent> i <PinkContent>fotowoltaiczne</PinkContent>. Specjalizujemy si w realizacji projektw dla nowo powstaych blokw, biurowcw i domw, zapewniajc bezpieczestwo i niezawodno.
                </Description>
                <LinkButton />
            </Wrapper>
            <Image src={vectorMap} alt="Poland"></Image>
        </Wrapper>

    </>
);

export default About;