import Hero from '../Hero';
import Section from '../Section';
import About from '../About';
import ServiceList from '../ServiceList';
import Slider from '../Slider';
import { slides } from '../../slides';

export default () => (
    <>
        <Hero />
        <Section body={<About />} />
        <Section
            isMiddle={true}
            title="Obszary dziaalnoci"
            content="Wybierz usug, z ktrej chcesz skorzysta, a my zajmiemy si reszt! Profesjonalna realizacja, nowoczesne rozwizania i najwysza jako usug."
            body={<ServiceList />}
        />
        <Section
            isMiddle={true}
            title="Co Mwi"
            extraTitle="O Nas Klienci"
            content="Zaufao nam setki klientw, docz do nich i bd zadowolony z wykonanej przez nas usugi"
            offMargin={true}
            body={<Slider slides={slides} />}
        />
    </>
);